___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: "Arial", sans-serif;
  background-color: #0E0E10;
  color: #f0f0f0;
}

header {
  text-align: center;
  margin: 0 auto;
  color: #f0f0f0;
  padding: 1em;
}
header .container {
  max-width: 1000px;
}

a {
  all: unset;
  cursor: pointer;
}

h2 {
  margin-top: 0;
}

.container {
  width: 90%;
  max-width: 1400px;
  margin: 0 auto;
}

.game-screen {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.player {
  background-color: #18181B;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  height: 500px;
  transition: 500ms;
}

.active-player {
  opacity: 50%;
}

.score-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-top: 20px;
  height: 200px;
  border-radius: 20px 20px;
  max-width: 500px;
  background-color: #18181B;
  color: #f0f0f0;
  font-weight: bold;
  font-size: 24px;
}

.game-grid {
  display: grid;
  width: 400px;
  height: 400px;
  grid-template-columns: repeat(10, 40px);
  grid-template-rows: repeat(10, 40px);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.game-cell {
  cursor: pointer;
  font-size: 26px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #1F1F25;
  border-radius: 5px;
}

.is-ship {
  background-color: #606068;
}

.is-hitted {
  background-color: #f16976;
}

.is-missed {
  background-color: #8fb9a8;
}

footer {
  display: flex;
  justify-content: center;
  margin-top: 25px;
  margin-bottom: 50px;
}
footer span {
  margin-left: 10px;
  font-size: 1rem;
}

.footer-content {
  display: flex;
  align-items: end;
  color: white;
  opacity: 50%;
  transition: 0.2s linear;
}

.footer-content:hover {
  opacity: 100%;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.scss"],"names":[],"mappings":"AAKA;EACI,gCAHU;EAIV,yBANc;EAOd,cANS;AAEb;;AAOA;EACI,kBAAA;EACA,cAAA;EACA,cAZS;EAaT,YAAA;AAJJ;AAMI;EACI,iBAAA;AAJR;;AAQA;EACI,UAAA;EACA,eAAA;AALJ;;AAQA;EACI,aAAA;AALJ;;AAQA;EACI,UAAA;EACA,iBAAA;EACA,cAAA;AALJ;;AAWA;EACI,aAAA;EACA,eAAA;EACA,uBAAA;EACA,mBAAA;AARJ;;AAYA;EACI,yBAjDY;EAkDZ,gBAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,YAAA;EACA,aAAA;EAEA,iBAAA;AAVJ;;AAaA;EACI,YAAA;AAVJ;;AAaA;EACI,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;EACA,YAAA;EACA,gBAAA;EACA,aAAA;EACA,wBAAA;EACA,gBAAA;EACA,yBA1EY;EA2EZ,cAzES;EA0ET,iBAAA;EACA,eAAA;AAVJ;;AAeA;EACI,aAAA;EACA,YAAA;EACA,aAAA;EACA,uCAAA;EACA,oCAAA;EACA,oBAAA;EACA,iBAAA;AAZJ;;AAeA;EACI,eAAA;EAEA,eAAA;EACA,iBAAA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,yBAAA;EACA,kBAAA;AAbJ;;AAgBA;EACI,yBAAA;AAbJ;;AAgBA;EACI,yBAAA;AAbJ;;AAgBA;EACI,yBAAA;AAbJ;;AAmBA;EACI,aAAA;EACA,uBAAA;EAEA,gBAAA;EACA,mBAAA;AAjBJ;AAmBI;EACI,iBAAA;EACA,eAAA;AAjBR;;AAqBA;EACI,aAAA;EACA,gBAAA;EACA,YAAA;EACA,YAAA;EACA,uBAAA;AAlBJ;;AAqBA;EAEI,aAAA;AAnBJ","sourcesContent":["$primary-color: #18181B;\n$secondary-color: #0E0E10;\n$font-color: #f0f0f0;\n$font-family: 'Arial', sans-serif;\n\nbody {\n    font-family: $font-family;\n    background-color: $secondary-color;\n    color: $font-color;\n}\n\nheader {\n    text-align: center;\n    margin: 0 auto;\n    color: $font-color;\n    padding: 1em;\n\n    .container {\n        max-width: 1000px;\n    }\n}\n\na {\n    all: unset;\n    cursor: pointer;\n}\n\nh2 {\n    margin-top: 0;\n}\n\n.container {\n    width: 90%;\n    max-width: 1400px;\n    margin: 0 auto;\n}\n\n\n// Game Screen\n\n.game-screen {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    align-items: center;\n}\n\n\n.player {\n    background-color: $primary-color;\n    margin-top: 20px;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 500px;\n    height: 500px;\n\n    transition: 500ms;\n}\n\n.active-player {\n    o